.certificateCards {
    width: 88%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 auto;
    justify-content: space-evenly;
}

.card {
    box-shadow: 0px 13px 27px -5px #888888;
}

.certificateCards .card:hover {
    cursor: pointer;
    transform: scale(1.03)
}